.srch-input {
    border: none;
    outline: none;
    border-radius: 15px;
    padding: 1em;
    margin: 1em;
    background-color: #e7e0ee;
    box-shadow: inset 2px 5px 10px rgba(0,0,0,0.3);
    transition: 300ms ease-in-out;
}
  
.srch-input:focus {
    background-color: white;
    transform: scale(1.05);
    box-shadow: 13px 13px 100px #969696,
                -13px -13px 100px #ffffff;
}